.ant-breadcrumb {
    padding: 1.5rem;
    position: fixed;
    z-index: 10;
    width: 100%;
}

.ant-breadcrumb > span {
    color: #DEBB00 !important; 
}

.ant-breadcrumb > span > span {
    color: #DEBB00 !important; 
}

.ant-breadcrumb > a {
    color: #DEBB00 !important; 
}

.about > div:not(:last-child) > div {
    background: none;
}

.about > div:not(:last-child) > div > p {
    font-size: 3rem;
    font-weight: bold;
    color: white;
    margin: 0;
    text-align: left;
    background-color: rgba(0,0,0,0);
    transition: 2s;
    color: #DEBB00;
}

.portfolio > div:nth-child(even) {
    height: 100vh;
    padding: 3rem 5rem;
    display: flex;
    align-items: flex-end;
    justify-content: center;
    flex-direction: column;
}

.portfolio > div:nth-child(odd) {
    height: 100vh;
    padding: 3rem 5rem;
    display: flex;
    align-items: flex-start;
    justify-content: center;
    flex-direction: column;
}

.right {
    top: 17%;
    height: 20.4rem;
    position: fixed;
    will-change: transform;
    transition: 0.4s;
    border-radius: 1rem;
    border: 1px lightgray solid;
    z-index: 10;
    margin: 5rem;
    object-fit: contain;
    transform: translate(110%, 0px) rotate(-4e-05deg) rotateY(-15deg) rotateX(9.99994deg);
    box-shadow: 38px 38px 10px 0px rgba(221, 186, 0, .7);
    right: 40rem;
    background: none !important;
}

.left {
    top: 17%;
    height: 20.4rem;
    position: fixed;
    will-change: transform;
    transition: 0.4s;
    border-radius: 1rem;
    border: 1px lightgray solid;
    z-index: 10;
    margin: 5rem;
    object-fit: contain;
    transform: translate(0%, 0px) rotate(-4e-05deg) rotateY(15deg) rotateX(9.99994deg);  
    box-shadow: -38px 38px 10px 0px rgba(221, 186, 0, .7);
}

.portfolio > div > p {
    width: 35vw;
    word-break: break-word;
    font-size: 1.25rem;
}

.portfolio > div:nth-child(odd) > p {
    color: white;
    float: left;
}

.portfolio > div:nth-child(even) > p {
    color: white;
    float: right;
}

.portfolio > div > .title {
    font-size: 2rem;
    color: #DEBB00 !important;
    font-weight: 700;
    display: flex;
    flex-direction: column;
}

.portfolio > div > .title > a {
    font-size: 1rem;
    color: #DEBB00 !important;
    font-weight: 300;
}

.portfolio > div > .title > a:hover {
    text-decoration: underline;
}